"use client"

import { Check, Clock } from "lucide-react"
import { useI18n } from "@/lib/i18n/context"

interface InvestmentStepperProps {
  currentStage: number
}

export function InvestmentStepper({ currentStage }: InvestmentStepperProps) {
  const { t } = useI18n()

  const stages = [
    { name: t("stages.pendingKyc"), description: t("stages.pendingKycDesc") },
    { name: t("stages.kycApproved"), description: t("stages.kycApprovedDesc") },
    { name: t("stages.agreementSigned"), description: t("stages.agreementSignedDesc") },
    { name: t("stages.paymentPending"), description: t("stages.paymentPendingDesc") },
    { name: t("stages.paymentConfirmed"), description: t("stages.paymentConfirmedDesc") },
    { name: t("stages.certificateIssued"), description: t("stages.certificateIssuedDesc") },
    { name: t("stages.activeShareholder"), description: t("stages.activeShareholderDesc") },
    { name: t("stages.inactive"), description: t("stages.inactiveDesc") },
    { name: t("stages.onHold"), description: t("stages.onHoldDesc") },
  ]

  const getStageStatus = (index: number) => {
    if (index < currentStage) return "completed"
    if (index === currentStage) return "current"
    return "pending"
  }

  const getStageIcon = (index: number) => {
    const status = getStageStatus(index)

    switch (status) {
      case "completed":
        return <Check className="h-5 w-5 text-white" />
      case "current":
        return <Clock className="h-5 w-5 text-white" />
      default:
        return <div className="h-3 w-3 rounded-full bg-white/30" />
    }
  }

  const getStageColor = (index: number) => {
    const status = getStageStatus(index)

    switch (status) {
      case "completed":
        return "bg-green-500"
      case "current":
        return "bg-[#eb6e03]"
      default:
        return "bg-slate-300"
    }
  }

  return (
    <div className="bg-white shadow-lg backdrop-blur-sm rounded-lg p-6 border border-slate-200">
      <h2 className="text-[#040956] text-xl font-semibold mb-6">{t("portal.investmentJourney")}</h2>

      <div className="relative">
        {/* Progress Line */}
        <div className="absolute left-6 top-8 bottom-8 w-0.5 bg-slate-200" />
        <div
          className="absolute left-6 top-8 w-0.5 bg-[#eb6e03] transition-all duration-500"
          style={{ height: `${(currentStage / (stages.length - 1)) * 100}%` }}
        />

        {/* Stages */}
        <div className="space-y-6">
          {stages.map((stage, index) => (
            <div key={index} className="relative flex items-start space-x-4">
              {/* Stage Icon */}
              <div
                className={`
                relative z-10 flex items-center justify-center w-12 h-12 rounded-full
                ${getStageColor(index)} transition-all duration-300 shadow-sm
              `}
              >
                {getStageIcon(index)}
              </div>

              {/* Stage Content */}
              <div className="flex-1 min-w-0 pb-6">
                <div className="flex items-center space-x-2">
                  <h3
                    className={`
                    font-semibold transition-colors duration-300
                    ${
                      getStageStatus(index) === "current"
                        ? "text-[#eb6e03]"
                        : getStageStatus(index) === "completed"
                          ? "text-green-600"
                          : "text-slate-500"
                    }
                  `}
                  >
                    {stage.name}
                  </h3>
                  {getStageStatus(index) === "current" && (
                    <div className="flex space-x-1">
                      <div className="w-2 h-2 bg-[#eb6e03] rounded-full animate-pulse" />
                      <div className="w-2 h-2 bg-[#eb6e03] rounded-full animate-pulse delay-75" />
                      <div className="w-2 h-2 bg-[#eb6e03] rounded-full animate-pulse delay-150" />
                    </div>
                  )}
                </div>
                <p
                  className={`
                  text-sm mt-1 transition-colors duration-300
                  ${getStageStatus(index) === "current" ? "text-[#040956]" : "text-slate-500"}
                `}
                >
                  {stage.description}
                </p>

                {/* Estimated Time for Current Stage */}
                {getStageStatus(index) === "current" && index === 0 && (
                  <div className="mt-2 flex items-center space-x-2 text-[#eb6e03] text-sm">
                    <Clock className="h-4 w-4" />
                    <span>
                      {t("stages.estimated")} 3-4 {t("stages.businessDays")}
                    </span>
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}
