import { useState, useEffect } from "react"
import { useNavigate } from "react-router-dom"
import { useAuthCheck } from "@/router/hooks/use-auth-check"
import { KYCForm } from "@/components/kyc-form"
import { InvestmentStepper } from "@/components/investment-stepper"
import { SuccessSplash } from "@/components/success-splash"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Button } from "@/components/ui/button"
import { LogOut, User } from "lucide-react"
import { useI18n } from "@/lib/i18n/context"

export default function KYCPortalPage() {
  const navigate = useNavigate()
  const { user, logout } = useAuthCheck()
  const [currentStage, setCurrentStage] = useState(0) // 0 = Pending KYC
  const [showSplash, setShowSplash] = useState(false)

  const { t } = useI18n()

  // Fetch user data and investment status
  useEffect(() => {
    fetchUserData()
  }, [])

  const fetchUserData = async () => {
    try {
      const token = localStorage.getItem("auth_token")
      const response = await fetch("/api/user/profile", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })

      if (!response.ok) {
        throw new Error("Failed to fetch user data")
      }

      const userData = await response.json()
      
      // Determine current stage based on user data
      if (userData.kyc_status === "completed") {
        setCurrentStage(1) // Move to investment stage
      }
    } catch (error) {
      console.error("Failed to fetch user data:", error)
    }
  }

  const handleKYCComplete = () => {
    setShowSplash(true)
    setTimeout(() => {
      setShowSplash(false)
      setCurrentStage(1) // Move to investment stage
    }, 3000)
  }

  const handleInvestmentComplete = () => {
    setShowSplash(true)
    setTimeout(() => {
      setShowSplash(false)
      // Could navigate to dashboard or show completed state
    }, 3000)
  }

  const handleLogout = () => {
    logout()
    navigate("/auth/signin", { replace: true })
  }

  if (showSplash) {
    return <SuccessSplash />
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100">
      <div className="container mx-auto px-4 py-8">
        {/* Header */}
        <div className="flex items-center justify-between mb-8">
          <div>
            <h1 className="text-4xl font-bold text-[#040956] mb-2">{t("portal.title")}</h1>
            <p className="text-slate-600 text-lg flex items-center">
              <User className="h-5 w-5 mr-2" />
              {t("portal.welcomeBack")}, {user?.name || "User"}
            </p>
          </div>
          <Button onClick={handleLogout} variant="outline" className="border-slate-300 hover:bg-slate-50">
            <LogOut className="h-4 w-4 mr-2" />
            {t("portal.logout")}
          </Button>
        </div>

        {/* Main Content */}
        <div className="max-w-4xl mx-auto">
          <Tabs value={currentStage === 0 ? "kyc" : "investment"} className="w-full">
            <TabsList className="grid w-full grid-cols-2 mb-8">
              <TabsTrigger 
                value="kyc" 
                className="data-[state=active]:bg-[#eb6e03] data-[state=active]:text-white"
                disabled={currentStage > 0}
              >
                {t("portal.kycVerification")}
              </TabsTrigger>
              <TabsTrigger 
                value="investment"
                className="data-[state=active]:bg-[#eb6e03] data-[state=active]:text-white"
                disabled={currentStage < 1}
              >
                {t("portal.investmentPlanning")}
              </TabsTrigger>
            </TabsList>

            <TabsContent value="kyc" className="space-y-6">
              <Card className="border-0 shadow-lg">
                <CardHeader>
                  <CardTitle className="text-2xl text-[#040956]">{t("portal.completeKyc")}</CardTitle>
                  <CardDescription className="text-lg">
                    {t("portal.kycDescription")}
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <KYCForm onComplete={handleKYCComplete} />
                </CardContent>
              </Card>
            </TabsContent>

            <TabsContent value="investment" className="space-y-6">
              <Card className="border-0 shadow-lg">
                <CardHeader>
                  <CardTitle className="text-2xl text-[#040956]">{t("portal.planInvestment")}</CardTitle>
                  <CardDescription className="text-lg">
                    {t("portal.investmentDescription")}
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <InvestmentStepper onComplete={handleInvestmentComplete} />
                </CardContent>
              </Card>
            </TabsContent>
          </Tabs>
        </div>
      </div>
    </div>
  )
}
